const lut: string[] = [];
for (let i: number = 0; i < 256; i++) {
    lut[i] = (i < 16 ? "0" : "") + i.toString(16).toUpperCase();
}

/**
 * Finished
 */
export class MathUtil {
    /**
     * /
     * @type {number}
     */
    public static DEG2RAD: number = Math.PI / 180;

    /**
     * /
     * @type {number}
     */
    public static RAD2DEG: number = 180 / Math.PI;

    /**
     * UUIDUUID
     * http://stackoverflow.com/questions/105034/how-to-create-a-guid-uuid-in-javascript/21963136#21963136
     * @returns {string}
     */
    public static generateUUID(): string {
        const d0 = (Math.random() * 0xffffffff) | 0;
        const d1 = (Math.random() * 0xffffffff) | 0;
        const d2 = (Math.random() * 0xffffffff) | 0;
        const d3 = (Math.random() * 0xffffffff) | 0;
        return (
            lut[d0 & 0xff] +
            lut[(d0 >> 8) & 0xff] +
            lut[(d0 >> 16) & 0xff] +
            lut[(d0 >> 24) & 0xff] +
            "-" +
            lut[d1 & 0xff] +
            lut[(d1 >> 8) & 0xff] +
            "-" +
            lut[((d1 >> 16) & 0x0f) | 0x40] +
            lut[(d1 >> 24) & 0xff] +
            "-" +
            lut[(d2 & 0x3f) | 0x80] +
            lut[(d2 >> 8) & 0xff] +
            "-" +
            lut[(d2 >> 16) & 0xff] +
            lut[(d2 >> 24) & 0xff] +
            lut[d3 & 0xff] +
            lut[(d3 >> 8) & 0xff] +
            lut[(d3 >> 16) & 0xff] +
            lut[(d3 >> 24) & 0xff]
        );
    }

    /**
     * value
     * @param value
     * @param min
     * @param max
     * @returns {number}
     */
    public static clamp(value: number, min: number, max: number): number {
        return Math.max(min, Math.min(max, value));
    }

    /**
     * 
     *  https://en.wikipedia.org/wiki/Modulo_operation
     * +m
     * @param n
     * @param m
     * @returns {number}
     */
    public static euclideanModulo(n: number, m: number): number {
        return (n % m + m) % m;
    }

    /**
     * xab
     * @param x
     * @param a1
     * @param a2
     * @param b1
     * @param b2
     * @returns {number}
     */
    public static mapLinear(x: number, a1: number, a2: number, b1: number, b2: number): number {
        return b1 + (x - a1) * (b2 - b1) / (a2 - a1);
    }

    /**
     * 
     * https://en.wikipedia.org/wiki/Linear_interpolation
     * @param x
     * @param y
     * @param t
     * @returns {number}
     */
    public static lerp(x: number, y: number, t: number): number {
        return (1 - t) * x + t * y;
    }

    /**
     * f(x) = 3 * x^2 - 2 * x^3
     * f"(x) = 6 * x - 6 * x^2 x \in [0, 1] \geq 0
     * f(x)[0, 1]
     * f(x)[0, 0.5][0.5 , 1]
     * 
     * http://en.wikipedia.org/wiki/Smoothstep
     * @param x
     * @param min
     * @param max
     * @returns {number}
     */
    public static smoothstep(x: number, min: number, max: number): number {
        if (x <= min) return 0;
        if (x >= max) return 1;
        x = (x - min) / (max - min);
        return x * x * (3 - 2 * x);
    }

    /**
     * 
     * @param x
     * @param min
     * @param max
     * @returns {number}
     */
    public static smootherstep(x: number, min: number, max: number): number {
        if (x <= min) return 0;
        if (x >= max) return 1;
        x = (x - min) / (max - min);
        return x * x * x * (x * (x * 6 - 15) + 10);
    }

    /**
     * 
     * @param low
     * @param high
     * @returns {number}
     */
    public static randInt(low: number, high: number): number {
        return low + Math.floor(Math.random() * (high - low + 1));
    }

    /**
     * 
     * @param low
     * @param high
     * @returns {number}
     */
    public static randFloat(low: number, high: number): number {
        return low + Math.random() * (high - low);
    }

    /**
     * [-range/2, range/2]
     * @param range
     * @returns {number}
     */
    public static randFloatSpread(range: number): number {
        return range * (0.5 - Math.random());
    }

    /*****************************  *******************************/

    public static degToRad(degrees: number): number {
        return degrees * MathUtil.DEG2RAD;
    }

    public static radToDeg(radians: number): number {
        return radians * MathUtil.RAD2DEG;
    }

    /**
     * 
     * @param value
     * @returns {boolean}
     */
    public static isPowerOfTwo(value: number): boolean {
        return (value & (value - 1)) === 0 && value !== 0;
    }

    public static ceilPowerOfTwo(value: number): number {
        return Math.pow(2, Math.ceil(Math.log(value) / Math.LN2));
    }

    public static floorPowerOfTwo(value: number): number {
        return Math.pow(2, Math.floor(Math.log(value) / Math.LN2));
    }
}
